  transform: translate(-0.4rem, -0.4rem);
}

.btn:active {
  box-shadow: 0 0 0 black;
  transform: translate(0, 0);
}

.options {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.hidden {
  display: none !important;
}

@keyframes text2 {
  0%,
    50%,
    100% {
    width: 0;
  }

  60%,
    90% {
    width: 15em;
  }
}

@keyframes text1 {
  0%,
    50%,
    100% {
    width: 0;
  }

  10%,
    40% {
    width: 14.5em;
  }
}

.text_1 {
  animation: text1;
}

.text_2 {
  animation: text2;
}

.text_1,
.text_2 {
  overflow: hidden;
  white-space: nowrap;
  display: inline-block;
  position: relative;
  animation-duration: 10s;
  animation-timing-function: steps(15, end);
  animation-iteration-count: infinite;
}

.text_1::after,
.text_2::after {
  content: "|";
  position: absolute;
  right: 0;
  animation: caret;
  animation-duration: 1s;
  animation-timing-function: steps(1, end);
}

@media (max-width:480px) {
  .modal {
    position: absolute;
    left: 50%;
    top: 60%;
    transform: translate(-50%, -50%);
    min-height: 13rem;
    width: 90%;
    max-width: 90vw !important;
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    padding: 1rem;
    border: 3px solid black;
    border-radius: 40px;
    background: white;
    opacity: 70%;
    box-shadow: 8px 8px 0 rgba(0, 0, 0, 0.2);
    font-family: monospace;
  }

  .btn {
    color: inherit;
    font-family: inherit;
    font-size: 1rem;
    background: white;
    padding: 0.3rem 2rem;
    border: 3px solid black;
    box-shadow: 0 0 0 black;
    transition: all 0.2s;
    border-radius: 40px;
    width: 50%;
    max-width: 40%;
  }

  .message {
    width: 90%;
    max-width: 90vw;
  }
}

