hr {
  border: none;
  height: 3px;
  /* Set the hr color */
  color: grey;
  /* old IE */
  background-color: grey;
  /* Modern Browsers */
  width: 50%;
  align-self: center;
}

.grow {
  transition: all .2s ease-in-out;
}

.grow:hover {
  transform: scale(1.6);
}

@media only screen and (max-width: 600px) {
  .grow:hover {
    transform: scale(2.2);
  }
}

